@import-normalize; /* bring in normalize.css styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.button {
  border: none;
  font: inherit;
  cursor: pointer;
  outline: none;
  margin-left: auto;
  border-radius: 4px;
  padding: 8px 12px;
  background-color: #3f68b5d1;
  color: #fff;
}

.button:hover,
.button:focus {
  background-color: #303f9f;
}

.button:active {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}
.container {

  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  &__login, &__register {
    @extend .container;
    flex-direction: column;
    min-height: 450px;

  }
}
.containerMessage{
  // border: 1px solid red;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  background-color: rgba(255, 0, 0, 0.557);
  border-radius: 5px;
  padding: 15px 20px;
  margin-bottom: 20px;
}
.errorMessage {
  font-weight: 500;
  color: rgb(255, 244, 200);
}

.loaderBlock {
  padding: 5px;
  padding-top: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;

  p {
    padding-top: 25px;
    text-align: center;
    font-size: 18px;
    color: rgba(42, 78, 108, 0.5);
  }
}

.form {
  position: relative;
  width: 550px;
  padding: 30px 15px 10px;
  // border: 1px solid rgba(128, 128, 128, 0.781);
  border-top: 2px solid rgba(65, 105, 225, 0.3);
  .label {
      display: flex;
      flex-direction: column;
      padding-bottom: 16px;
  
      input {
        display: block;
        width: 100%;
        font-size: 16px;
        height: calc(2.25rem + 2px);
        padding: 5px 12px;
        margin-top: 5px;
        color: #495057;
        background-color: #fff;
        background-clip: padding-box;
        border: 1px solid #ced4da;
        border-radius: 0.25rem;
        transition: border-color .15s ease-in-out, box-shadow .15s ease-in-out;
      }
    }
}